.preview {
	position: relative;
	// margin-top: -91px
	padding-top: 155px;
	padding-bottom: 302px;
	color: $white;
	overflow: hidden;
	@include cover;
	@include clr;
	@include md {
		overflow: hidden;
		padding-bottom: 0; }
	&:before {
		position: absolute;
		content: "";
		bottom: 0;
		left: 3vw;
		margin-left: calc(50% - 15vw);
		margin-bottom: 19.5vw;
		z-index: 1;
		width: 97vw;
		height: 160px;
		transform-origin: right bottom;
		transform: skew(0deg, -11.5deg);
		pointer-events: none;
		background: $white;
		display: none;
		@include md {
			display: block; } }
	&:after {
		position: absolute;
		content: "";
		bottom: 0;
		left: 0;
		right: 0;
		height: 160px;
		background: $white;
		display: none;
		@include md {
			display: block; } }
	&__in {
		max-width: 1170px;
		@include clr; }
	&__image {
		position: absolute;
		top: -8px;
		left: -532px;
		width: 930px;
		@include md {
			top: 18px;
			left: 50%;
			margin-left: -378px; }
		&-wrapper {
			position: relative;
			float: right;
			z-index: 1;
			pointer-events: none;
			@include md {
				float: none;
				height: 495px; } }
		&-wrapper img {
			max-width: 100%;
			display: block; } }
	&__desc {
		float: left;
		max-width: 670px;
		z-index: 3;
		position: relative;
		padding-left: 70px;
		@include md {
			padding-left: 0;
			float: none; }
		.title-lead {
			margin-top: 71px; }
		.title-shaped {
			margin-left: 65px; } }
	&__text {
		padding-left: 100px;
		margin-top: 50px;
		@include md {
			padding-left: 58px;
			margin-top: 25px;
			text-align: center; }
		p {
			color: $gray;
			@include font-family($gotham-light);
			font-size: 14px;
			line-height: 2.14286;
			text-align: left;
			strong {
				text-transform: uppercase;
				@include font-family($gotham-md); } }
		.btn {
			margin-top: 45px;
			max-width: 228px;
			height: 68px;
			font-size: $text-reg; //16px
			line-height: 68px;
			@include r(768) {
				margin-top: 45px; } } }
	.copyright {
		@include font-family($gotham-light);
		color: $gray;
		position: absolute;
		left: 38px;
		bottom: 0;
		padding-top: 350px;
		opacity: 0.4;
		letter-spacing: 0.2em;
		writing-mode: vertical-lr;
		transform: rotate(180deg);
		@include r(1199) {
			left: 3px; }
		&:after {
			content: "";
			display: block;
			position: absolute;
			top: 0;
			transform: translate3d(0px,9px,0px) rotateZ(0deg);
			right: 100%;
			width: 340px;
			height: 1px;
			background-color: rgba($gray, .40); } } }
.order {
	padding: 100px 0 95px 0;
	@include image(bg-order);
	@include cover;
	text-align: center;
	@include r(768) {
		padding: 60px 0; }
	@include r(667) {
		padding: 40px 0; }
	h3 {
		margin-bottom: 50px;
		text-transform: uppercase;
		color: $main;
		font-size: $text-lg; //36px
		@include font-family($gotham-reg);
		@include r(768) {
			margin-bottom: 30px; } }
	form {
		max-width: 770px;
		margin: 0 auto;
		font-size: 0;
		@include r(860) {
			max-width: 90%; }
		.grid {
			&__item {
				@include r(860) {
					width: 100%;
					padding-right: 0;
					margin-bottom: 15px; }
				@include r(640) {
					width: 50%;
					padding-right: 15px; }
				@include r(568) {
					width: 100%;
					padding-right: 0; } }
			&-double-spacing {
				@include r(860) {
					margin-right: 0; }
				@include r(640) {
					margin-right: -15px; }
				@include r(568) {
					margin-right: 0; } } } }
	.textarea-field {
		margin: 15px auto 70px;
		@include r(860) {
			margin: 0 auto 30px; } } }


// features
.features {
	background-image: url("../img/bg-features.png");
	@include cover;
	display: inline-block;
	width: 100%;
	position: relative;
	@include md {
		background-position: left center; }
	&__in {
		position: relative; }
	.image-left {
		position: absolute;
		left: 25px;
		margin-left: -108px;
		bottom: 0;
		width: 550px;
		height: 897px;
		overflow: hidden;
		img {
			display: block;
			margin: 0 auto;
			max-width: 100%; } }
	&__right {
		float: right;
		font-size: 0;
		max-width: 670px;
		width: 61%;
		position: relative;
		top: -77px;
		@include md {
			top: 0; }
		.list-features {
			width: 50%;
			max-width: 340px;
			display: inline-block;
			vertical-align: top;
			&.offset-top {
				margin-top: 115px; }
			@include r(992) {
				max-width: 100%; } }
		.title-shaped {
			top: 0;
			right: 40px;
			position: absolute; } } }


// register
.register {
	overflow: hidden;
	@include clr;
	background-image: url("../img/bg-dark.png");
	@include cover;
	padding: 100px 0 104px 0;
	&-preview {
		position: absolute;
		display: inline-block;
		width: 740px;
		top: -40px;
		right: -220px;
		vertical-align: top;
		@include r(1166) {
			width: 680px;
			top: 70px;
			right: -250px; }
		@include r(992) {
			top: 108px;
			position: relative;
			right: 0;
			left: 0;
			width: 100%;
			height: 815px; }
		@include r(768) {
			height: 605px; }
		img {
			max-width: 100%;
			height: auto; } }
	.inner {
		@include r(768) {
			padding: 0 30px; }
		&__in {
			position: relative;
			@include r(768) {
				padding: 0 30px; } } }
	&__desc {
		float: left;
		padding-left: 45px;
		max-width: 675px;
		@include r(768) {
			padding-left: 0;
			float: none;
			max-width: 100%; }
		.title-shaped {
			margin-left: 61px; }
		h3 {
			font-size: 60px;
			letter-spacing: 0.20rem;
			color: $white;
			line-height: 1.11;
			@include font-family($gotham-reg);
			margin: 52px auto 17px;
			text-transform: uppercase; }
		p.price {
			@include font-family($gotham-reg);
			font-size: 119.7px;
			color: $brown2;
			line-height: 1;
			letter-spacing: 0.05rem;
			strong {
				@include font-family($gotham-md);
				display: inline-block;
				font-size: 80px; } } }
	&__about {
		margin-top: 70px;
		.btn-group {
			margin-top: 60px;
			.btn {
				@include r(992) {
					max-width: 231px; } } }
		p {
			@include font-family($gotham-light);
			font-size: 14px;
			line-height: 2.13;
			color: $gray;
			strong {
				@include font-family($gotham-bld);
				text-transform: uppercase;
				font-weight: bold; } }
		.title-icon {
			font-size: 14px;
			color: $gray;
			text-transform: uppercase;
			position: relative;
			padding-bottom: 29px;
			@include font-family($gotham-light);
			margin-bottom: 19px;
			&:after {
				content: "";
				display: block;
				position: absolute;
				left: 0;
				top: 100%;
				background-color: rgba($coffee, 0.45);
				height: 1px;
				width: 60px; }
			i {
				position: absolute;
				top: -12px;
				left: -50px; } } } }


// gallery
.gallery {
	display: inline-block;
	vertical-align: top;
	@include clr;
	width: 100%; }
.js-reveal {
	opacity: 1;
	@include r(680) {
		transform: translate3d(0, 0, 0) !important; }
	&.active {
		opacity: 0;
		background-color: red; } }

.item {
	&-slider {
		overflow: hidden;
		display: inline-block;
		vertical-align: top;
		&:hover a {
			transform: scale(1.1);
			@include r(768) {
				transform: scale(1); } }
		a {
			@include cover;
			text-decoration: none;
			transition: transform .42s ease-in-out;
			cursor: pointer;
			display: block; }
		// item sizes
		&.item {
			&-lg {
				width: 65.5%;
				@include r(640) {
					height: 630px; }
				@include r(570) {
					height: auto; }
				a {
					padding-bottom: 50.56%;
					@include r(640) {
						padding-bottom: 100%; } } }
			&-md {
				width: 34.50%;
				@include r(640) {
					height: 630px; }
				@include r(570) {
					height: auto; }
				a {
					padding-bottom: 94.58%;
					@include r(640) {
						padding-bottom: 100%; } } }
			&-sm {
				width: 33.3333%;
				@include r(640) {
					height: 630px; }
				@include r(570) {
					height: auto; }
				a {
					padding-bottom: 73.40%; } } } }
	&-slider img {
		max-width: 100%;
		height: auto; } }

.slick {
	&-dots {
		text-align: center;
		position: absolute;
		bottom: 40px;
		left: 50%;
		transform: translateX(-50%);
		@include r(570) {
			bottom: 20px; }
		li {
			display: inline-block;
			vertical-align: middle;
			line-height: 15px;
			padding: 0 10px;
			button {
				background-color: $coffee;
				font-size: 0;
				width: 9px;
				vertical-align: middle;
				height: 9px;
				transition: all .24s ease-in-out;
				border-radius: 50px; }
			&.slick-active {
				button {
					background-color: rgba($coffee, .50);
					transform: scale(1.70); } } } } }
