.btn {
  cursor: pointer;
  max-width: 261px;
  padding: 0;
  width: 100%;
  display: inline-block;
  vertical-align: top;
  background-color: transparent;
  text-transform: uppercase;
  letter-spacing: 0.200rem;
  font-family: "gothampro-regular", Helvetica Neue, Helvetica, Arial, sans-serif;
  height: 68px;
  outline: none;
  text-decoration: none;
  text-align: center;
  line-height: 68px;
  z-index: 5;
}

.btn:focus, .btn:hover {
  outline: none;
}

.btn-ghost {
  transition: border-color .32s ease, background-color .34s ease-in-out;
  position: relative;
}

.btn-ghost:before {
  bottom: -8px;
  left: 0;
  width: calc(100% + 8px);
  height: 1px;
}

.btn-ghost:after {
  right: -8px;
  height: calc(100% + 8px);
  width: 1px;
  top: -1px;
}

.btn-ghost:before, .btn-ghost:after {
  content: "";
  display: block;
  position: absolute;
  transition: background-color .32s ease;
}

.btn-dark {
  color: #162332;
  border: 1px solid #162332;
}

.btn-dark:before, .btn-dark:after {
  background-color: rgba(22, 35, 50, 0.3);
}

.btn-dark:hover {
  border-color: rgba(22, 35, 50, 0.3);
  background-color: rgba(22, 35, 50, 0.3);
}

.btn-dark:hover:before, .btn-dark:hover:after {
  background-color: #060a0f;
}

.btn-brown {
  color: #c8b29a;
  border: 1px solid #c8b29a;
}

.btn-brown:before, .btn-brown:after {
  background-color: rgba(200, 178, 154, 0.3);
}

.btn-brown:hover {
  border-color: rgba(200, 178, 154, 0.3);
  background-color: rgba(200, 178, 154, 0.3);
}

.btn-brown:hover:before, .btn-brown:hover:after {
  background-color: #b69979;
}

.btn-nav {
