    display: block;
  }
}

.btn-nav:hover span {
  background-color: #7a6e61;
}

.btn-nav:hover span:after,
.btn-nav:hover span:before {
  background-color: #7a6e61;
}

.btn-nav span {
  position: absolute;
  -webkit-transition: all .24s ease-in-out;
